    color: #62656a;

    .copyright {
        float: right;
        margin: 0;
        font-size: 12px;
        line-height: 37px;
    }

    a {
        display: inline-block;

        &:hover {
            color: $primary;
            text-decoration: none;
        }
    }

    i {
        font-size: 25px;
        color: #888;
        margin-right: 10px;

        &:hover {
